@use "sass:math";

// 
// SPPC
$break-flg: 0 !default; //0:SP1PC

$breakpoints: null;
@if $break-flg == 0 {
  $breakpoints: (
    "sm": "screen and (min-width: 400px)",
    "md": "screen and (min-width: 768px)",
    "lg": "screen and (min-width: 1000px)",
    "xl": "screen and (min-width: 1200px)",
    "mw": "screen and (max-width: 767px)",
  );
} @else {
  // 
  $breakpoints: (
    "sm": "screen and (max-width: 399px)",
    "md": "screen and (max-width: 767px)",
    "lg": "screen and (max-width: 999px)",
    "xl": "screen and (max-width: 1199px)",
  );
}

// inner
$inner: 1200px;

// 
$padding-pc: 25px; // PC
$padding-sp: 20px; // 

// 
$font-family-base: Meiryo, "", "Hiragino Kaku Gothic Pro", " Pro W3", "MS PGothic", " ", sans-serif; // 

$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

// 
$base-font-size: 20px;

// Color
$color-black:#333;
$color-red:#e00;
$color-white:#fff;
$color-green:#00890E;
$color-orange:#FF9213;
$color-background: #FBF6EF;
