@use '../foundation' as *;

/*==================================================

===================================*/

.delay > * {
    opacity: 0;
}

/*==================================================

===================================*/

.fadeUp {
    animation-name: fadeUpAnime;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
    animation-timing-function: ease-out;
    opacity: 0;
}

@keyframes fadeUpAnime {
    from {
        opacity: 0;
        transform: translateY(2vw);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}
.btnAnimation {
    &:hover {
        display: inline-block;
        opacity: 0.8;
        // transform: translate(-6px, -6px);
    }
}

.fadein {
    @media screen and (max-width: 838px) {
        opacity: 0;
        transform: translate(0, 100px);
        transition: all 1s;

        &.active {
            opacity: 1;
            transform: translate(0, 0);
        }
    }
}

.fadein_pc {
    opacity: 0;
    transform: translate(0, 100px);
    transition: all 1s;

    &.active {
        opacity: 1;
        transform: translate(0, 0);
    }
}

/*===================================
 * part2
===================================*/
/**/
.leftAnime {
    opacity: 0; /*0*/
}

.slideAnimeLeftRight {
    animation-name: slideTextX100;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    opacity: 0;
}

@keyframes slideTextX100 {
    from {
        transform: translateX(-100%); /**/
        opacity: 0;
    }

    to {
        transform: translateX(0); /**/
        opacity: 1;
    }
}

.slideAnimeRightLeft {
    animation-name: slideTextX-100;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    opacity: 0;
}

@keyframes slideTextX-100 {
    from {
        transform: translateX(100%); /**/
        opacity: 0;
    }

    to {
        transform: translateX(0); /**/
        opacity: 1;
    }
}

/*===================================
 * hover 
===================================*/
.u-hover-img {
    cursor: pointer;
    max-width: 500px;
    overflow: hidden;
    width: 100%;
}
.u-hover-img img {
    height: auto;
    transition: transform 0.8s ease !important; /*  */
}
.u-hover-img:hover img {
    transform: scale(1.1) !important; /*  */
    opacity: 0.8 !important;
}

/*===================================
 * 
===================================*/

.btn03 {
    /*relative*/
    position: relative;
    /**/
    text-decoration: none;
    text-align: center;
    border-radius: 25px;
    outline: none;
    /**/
    transition: all 0.2s ease;

    &::before {
        content: '';
        position: absolute;
        background-image: url('../img/icon/icon_hand.svg');
        background-size: contain;
        background-repeat: no-repeat;
        display: block;
        width: rem(30);
        height: rem(30);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 10%;
        @include mq(sm) {
            right: 0;
        }
    }
}

/**/
.pushdown::after {
    content: '';
    /**/
    position: absolute;
    z-index: -1;
    top: -4px;
    left: 0;
    /**/
    width: 100%;
    height: 100%;
    border-radius: 25px;
    background-color: $color-green;
}

/*hoverY4px*/
.pushdown:hover {
    background-color: #333;
    color: #fff;
    transform: translateY(-4px);
}

/*===================================
 * 
===================================*/

/**/
.scrolldown4 {
    /**/
    position: absolute;
    top: 18%;
    right: 50%;
    /*1*/
    animation: arrowmove 1s ease-in-out infinite;
    @include mq(md){
     display: none;
    }
}

/**/
@keyframes arrowmove {
    0% {
        right: 1%;
    }
    50% {
      right: 3%;
    }
    100% {
      right: 1%;
    }
}

/*Scroll*/
.scrolldown4 span {
    /**/
    position: absolute;
    left:  rem(-130);
    bottom: 10px;
    /**/
    color: $color-green;
    font-size: 0.7rem;
    letter-spacing: 0.05em;

}

/*  */
.scrolldown4:before {
    content: '';
    /**/
    position: absolute;
    bottom: 0;
    right: 8px;
    /**/
    width: 2px;
    height: 20px;
    background: $color-green;
    transform: skewX(45deg);
}

.scrolldown4:after {
    content: '';
    /**/
    position: absolute;
    bottom: 0;
    right: 0;
    /**/
    width: rem(150);
    height: rem(2);
    background: $color-green;
}
